.display input {
    width: 100%;
    height: 60px;
    margin-bottom: 10px;
    padding: 5px;
    font-size: 2.5rem;
    text-align: end;
    border: 0;
    color: var(--white);
}

.display input:focus{
    outline: none;
}

.display input:disabled{
    background-color: var(--display);
}